'use strict';
// –û–ø–∏—Å–∞–Ω–∏–π —É –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü—ñ—ó
import iziToast from "izitoast";
// –î–æ–¥–∞—Ç–∫–æ–≤–∏–π —ñ–º–ø–æ—Ä—Ç —Å—Ç–∏–ª—ñ–≤
import "izitoast/dist/css/iziToast.min.css";
// –û–ø–∏—Å–∞–Ω–∏–π —É –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü—ñ—ó
import SimpleLightbox from "simplelightbox";
// –î–æ–¥–∞—Ç–∫–æ–≤–∏–π —ñ–º–ø–æ—Ä—Ç —Å—Ç–∏–ª—ñ–≤
import "simplelightbox/dist/simple-lightbox.min.css";

const form = document.querySelector('.form');
const gallery = document.querySelector('.gallery');
const loader = document.querySelector('.loader');

const searchParameters = {
  key: '41869346-1dc68e1b0e88fdca1b815defb',
  q: 'black',
  image_type: 'photo',
  orientation: 'horizontal',
  safesearch: true,
};

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
  
async function searchImg(params) {

        try {
            const response = await fetch(`https://pixabay.com/api/?${params}`);
            if (!response.ok) {
                throw new Error(response.statusText);
            }
            const { hits } = await response.json();
            if (hits.length > 0) {
                const renderImg = hits.reduce((html, hit) => {
                    return (
                        html +
                        `<li class="gallery-item">
        <a href=${hit.largeImageURL}> 
          <img class="gallery-link" src ='${hit.webformatURL}' alt='${hit.tags}' width='360' height='200'/>
        </a>
        <ul class='gallery-statistic'>
                  <li><p class='statistic'>üíó Likes<span>${hit.likes}</span></p></li>
                  <li><p class='statistic'>üëÅÔ∏è Views<span>${hit.views}</span></p></li>
                  <li><p class='statistic'>üí¨ Comments<span>${hit.comments}</span></p></li>
                  <li><p class='statistic'>üíå Downloads<span>${hit.downloads}</span></p></li>
              </ul>
      </li>`
                    );
                }, '');
                gallery.innerHTML = renderImg;

                lightbox.refresh();
            } else {
                iziToast.error({
                    position: 'topRight',
                    message: 'Sorry, there are no images matching your search query. Please try again!',
                    backgroundColor: '#ef4040',
                    messageSize: '16px',
                    messageColor: '#fafafb',
                });
            }
        } catch (error) {
            iziToast.error({ 
            position: 'topRight',
            message: 'Sorry, there was an error fetching the data. Please try again!',
            backgroundColor: '#ef4040',
            messageSize: '16px',
            messageColor: '#fafafb',
        });
        }
     finally {
      loader.style.display = 'none';
    }
}
form.addEventListener('submit', event => {
  event.preventDefault();
    gallery.innerHTML = '';
    loader.style.display = 'block';


  searchParameters.q = event.target.elements.search.value.trim();
  const searchParams = new URLSearchParams(searchParameters);
  searchImg(searchParams);
  event.currentTarget.reset();
});
